import React from 'react';

import { styled, Card, CardContent, CardHeader } from '@material-ui/core';

const DivWithPointer = styled(({ ...props }) => <div {...props} />)({
  cursor: 'pointer',
});

export const Passage = ({ data }) => {
  const { passage } = data;
  const { book, verses } = passage;

  return (
    <Card>
      <CardHeader title={book} />
      <CardContent>
        {verses.map(verse => {
          const { content, number } = verse;

          return (
            <DivWithPointer key={content}>
              <sup>{number}</sup>
              <span>{content}</span>
            </DivWithPointer>
          );
        })}
      </CardContent>
    </Card>
  );
};
