import React from 'react';

import { Card, CardContent, CardHeader } from '@material-ui/core';

export const CrossReference = ({ data }) => {
  const { crossReferences } = data;

  return (
    <Card>
      <CardHeader title="Cross-references" />
      <CardContent>
        {crossReferences.map(crossReference => (
          <div key={crossReference}>{crossReference}</div>
        ))}
      </CardContent>
    </Card>
  );
};
